body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  color: #fff;
  text-align: center;
  background-color: #4e4ede;
  padding: 20px;
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  padding: 20px;
  display: grid;
}

.photo-card {
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-radius: 8px 8px 0 0;
}

.info {
  text-align: center;
  padding: 16px;
}

.info-item {
  margin: 5px 0;
  font-size: 14px;
}

